<template>
  <div class="collection">
    <div class="navtop">
      <van-nav-bar title="我的收藏" left-arrow @click-left="back" />
    </div>

    <!-- 收藏商品列表 -->
    <ul class="collecList" v-if="collecdata.length > 0">
      <router-link
        v-for="(item, i) in collecdata"
        :key="i"
        :to="`/details/${item.pid}`"
        tag="li"
      >
        <img :src="item.image" />
        <div class="text">
          <p class="title">{{ item.store_name }}</p>
          <div class="priceBox">
            <span class="price">￥{{ item.price }}</span>
            <span class="del" @click.stop="cancelCollec(item.pid)">取消收藏</span>
          </div>
        </div>
      </router-link>
    </ul>

    <div class="img" v-else>
      <img src="../assets/image/collecEmpty.png">
      <p>暂无收藏商品</p>
    </div>

  </div>
</template>

<script>
import "../assets/style/collection.less";
export default {
  name: "Collection",
  data() {
    return {
      collecdata: [],
    };
  },
  created() {
    this.getCollectionData();
  },
  methods: {
    // 返回
    back() {
      this.$router.back();
    },

    // 获取我收藏的商品
    getCollectionData() {
      // 判断token存不存在
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/collect/user?page=1&limit=20",
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          console.log(res);
          this.$toast.clear();

          if (res.data.status == 410000) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 1000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.status === 200) {
            this.collecdata = res.data.data;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 1000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    // 取消收藏
    cancelCollec(id) {
      // 判断token存不存在
      let tokenString = this.$cookies.get("tokenString");

      if (!tokenString) {
        console.log("浏览器token不存在");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/collect/del",
        data: {
          category: "product",
          id
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          this.$toast.clear();

          if (res.data.status == 410000) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 1000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.status === 200) {
            this.$toast({
              message: "取消收藏成功" ,
              forbidClick: true,
              duration: 1000,
            });

            this.getCollectionData()

          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 1000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },
  },
};
</script>